<template>
  <div class="login">
    <div class="login_context">
      <!--    头部图片-->
      <div class="login_logo">
        <img src="@/assets/Logo2.png">
      </div>
      <!--    form表单-->
      <el-form ref="editFormRef" :model="ruleForm" :rules="editFormRules" class="login_box">
        <el-form-item prop="mobile">
          <el-input v-model="ruleForm.mobile" prefix-icon="el-icon-user"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input v-model="ruleForm.password" prefix-icon="el-icon-lock" type="password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login()">登录</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  name: 'LoginView',
  data() {
    //验证手机号码的规则
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^1[345789]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的手机号码'))
    };
    return {
      user: {},
      ruleForm: {
        mobile: '',
        password: '',
      },
      editFormRules: {
        mobile: [
          {validator: checkMobile, trigger: 'blur'}
        ],
      }
    };
  },
  methods: {
    //登录
    login() {
      this.$refs['editFormRef'].validate((valid) => {
        if (valid) {
          const _this = this
          this.$axios.post('/adminLogin', this.ruleForm).then(res => {
            //判断成功或失败
            if (res.data.status != 10000) {
              this.$message.error("登录失败")
            } else {
              this.$message.success("登录成功！")
              this.user = res.data.data;
              // console.log(this.user)
              //-----------------------------------------------------------------------
              // const jwt = res.headers['USER_LOGIN_TOKEN']
              const jwt = res.data.data.token;
              // console.log(jwt)
              // _this是发送请求之前存起来的旧的this,调用$store.commit()用于保存到前端全局数据
              _this.$store.commit("SET_TOKEN", jwt);
              //保存当前的token到会话存储
              window.sessionStorage.setItem('token', jwt)
              //------------------------------------------------------------------------

              //------------------------------------------------------------------------
              // 新增的
              _this.$store.commit("setUserName", this.user.username);
              _this.$store.commit("setStatus", this.user.status);
              _this.$store.commit("setUserInfo", this.user);
              _this.$store.commit("SET_USERINFO", this.user)
              //保存当前的用户信息到会话存储
              window.sessionStorage.setItem('UserInfo', JSON.stringify(this.user))
              //------------------------------------------------------------------------------

              //登录成功，跳转到home页面
              _this.$router.push('/home')
            }
          });
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm() {
      this.$refs["editFormRef"].resetFields();
    }
  }
}
</script>

